const { userModal } = require('../../../db')
const jwt = require('jsonwebtoken')

export default async function handler(req, res) {
    const { token } = req.query
    try {
        const decoded = jwt.verify(token, process.env.JWT_PASS)
        await userModal.findOneAndUpdate({ username: decoded.username }, { isPremium: true })

        // Redirect back to admin inbox
        res.setHeader('Location', `/admin/message/${decoded.username}?premium=true`)
        res.statusCode = 302
        res.end()
    } catch (e) {
        res.status(500).send("Upgrade failed")
    }
}
